<template>
  <div class="orderDetail mescroll">
    <div class="logistics" v-if="detail.orderStatus != 9">
      <div class="waitPay" v-if="detail.orderStatus == 1 ">
        <p class="title">等待买家付款</p>
        <p class="text">
          <!-- 剩余
          <span v-if="hour">{{addZero(hour)}}小时</span>
          {{addZero(min)}}分后自动关闭-->
          请尽快付款
        </p>
      </div>
      <!-- <div class="flex" @click="toLogistics" v-else>
        <img src="../../assets/img/icon/wul.png" class="icon" />
        <div class="flex-2">
          <div class="text">{{detail.trace.AcceptStation}}</div>
          <div class="time">{{detail.trace.AcceptTime}}</div>
        </div>
        <div class="arrow">
          <img src="../../assets/img/icon/left.png" alt />
        </div>
      </div>-->
    </div>
    <div class="address flex" v-if="detail.orderType != 7">
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">收货人：{{detail.addr?detail.addr.userName2:''}}</span>
          <span class="tenlo">{{detail.addr?detail.addr.userMobile:''}}</span>
        </div>
        <div class="addressInfo">{{detail.addrText?detail.addrText:''}}</div>
      </div>
    </div>
    <div class="address flex" v-else>
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">提货人：{{detail.userName2?detail.userName2:''}}</span>
          <span class="tenlo">{{detail.userMobile?detail.userMobile:''}}</span>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="goodsInfo">
      <div class="goodsInfo-top flex">
        <div class="flex-2 title">商品信息</div>
        <div class="status" :style="detail.orderStatus == 1 || detail.orderStatus == 3?'color:#eb5252':detail.orderStatus == 4?'color:#333':'color:#999'">{{$utils.getText(detail.orderStatus)}}</div>
      </div>
      <div class="list">
        <div class="item flex" v-for="(item,index) in detail.scEcomOrdersDetailList" :key="index">
          <div class="item-pic flex-1">
            <img :src="$utils.dealImg(item.scEcomGoods.gimg)" />
          </div>
          <div class="flex-2">
            <div class="title">{{item.scEcomGoods.gname}}</div>
            <div class="price">
              ¥
              <span class="money">{{item.scEcomGoods.goutPrice?item.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</span>
              <span class="num">×{{item.gOrderCount}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="goodsInfo-bottom">
        <div class="flex" v-if="false">
          <div class="flex-1" style="color:#333">使用卡券</div>
          <div class="flex-2">否</div>
        </div>
        <div class="flex1" v-if="detail.remarks">
          <div class="flex-1">备注</div>
          <div class="flex-2">{{detail.remarks}}</div>
        </div>
        <div class="line1"></div>
        <div class="flex">
          <div class="flex-1">商品数量</div>
          <div class="flex-2">{{detail.shopCount}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">商品合计</div>
          <div class="flex-2">￥{{detail.oOrderMoney?detail.oOrderMoney.toFixed(2):'0.00'}}</div>
        </div>
        <div class="flex" v-if="detail.oOrderYunFei">
          <div class="flex-1">运费合计</div>
          <div class="flex-2">￥{{detail.oOrderYunFei.toFixed(2)}}</div>
        </div>
        <div class="flex" v-if="detail.oOrderYouhui">
          <div class="flex-1">优惠合计</div>
          <div class="flex-2">￥{{detail.oOrderYouhui.toFixed(2)}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">实付款</div>
          <div class="flex-2 allPrice">
            ￥
            <span>{{detail.oOrderMoneyShifu?detail.oOrderMoneyShifu.toFixed(2):'0.00'}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="orderInfo" :style="detail.orderStatus == 2 ||detail.orderStatus  == 1?'padding-bottom:70px':''">
      <!-- :style="detail.orderStatus  == 1 || detail.orderStatus  == 3 || detail.orderStatus  == 5?'padding-bottom:70px;':''" -->
      <div class="title">订单信息</div>
      <div class="flex">
        <div class="flex-1">订单编号</div>
        <div class="flex-2">{{detail.orderno}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">下单时间</div>
        <div class="flex-2">{{detail.oOrderWdate}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 9">
        <div class="flex-1">支付方式</div>
        <div class="flex-2">{{detail.pay_type == 1?'余额':'微信'}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 9">
        <div class="flex-1">支付时间</div>
        <div class="flex-2">{{detail.oPayWdate}}</div>
      </div>
    </div>
    <div class="bottom" v-if="detail.orderStatus  == 2 || detail.orderStatus  == 1">
      <div class="btns flex">
        <div class="btn btnPay" v-if="detail.orderStatus  == 3" @click="handle(1)">确认收货</div>
        <div class="btn btnPay" v-if="detail.orderStatus  == 1" @click="handle(3)">去付款</div>
        <!-- <div class="btn" v-if="detail.orderStatus  == 5 " @click="handle(5)">删除订单</div>  -->
         <div class="btn" v-if="detail.orderStatus  == 1" @click="handle(6)">取消订单</div>
        <img v-if="detail.orderStatus == 2" @click="showQrcode()" class="qrcode" src="../../assets/img/icon/qrcode.png" alt />
      </div>
    </div>
    <van-popup v-model="show">
      <div class="popup-mid" @click="show = false">
        <img :src="qrcode" alt />
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Dialog, Popup } from "vant";
import {
  getOrderDetail,
  getQrcode,
  weixinPay,
  cancelOrder,
  //deleteOrder,
  deliveryGoods
} from "@/api";
export default {
  components: {
    [Popup.name]: Popup,
  },
  data() {
    return {
      hour: 2,
      min: 1,
      detail: {},
      show: false,
      qrcode: "",
    };
  },
  methods: {
    toLogistics() {
      this.$router.push({
        name: "logistics",
        query: { id: this.detail.orderCode },
      });
    },
    showQrcode(id) {
      getQrcode({
        url:
          this.$config.http +
          "/wx/#/closeOrderDetail?type=1&id=" +
          this.detail.orderCode +
          "&orderCode=" +
          this.detail.orderCode,
      }).then(res => {
        if (res.code == 0) {
          this.qrcode = res.data.qrCode;
          this.show = true;
        }
      });
    },
    handle(type) {
      let that = this;
      switch (type) {
        case 1:
          //确认收货
          Dialog.confirm({
            title: "提示",
            message: "是否确定收货？",
          })
            .then(() => {
              // on confirm
              that.delivery();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 2:
          //查看物流
          this.toLogistics();
          break;
        case 3:
          //去付款
          Dialog.confirm({
            title: "提示",
            message: "是否确定付款？",
          })
            .then(() => {
              // on confirm
              that.makePreOrder();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 4:
          //联系客服
          break;
        case 5:
          //删除订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定删除？",
          })
            .then(() => {
              // on confirm
              that.delete();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 6:
          //取消订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定取消？",
          })
            .then(() => {
              // on confirm
              that.cancel();
            })
            .catch(() => {
              // on cancel
            });
          break;
        default:
          console.log(type);
      }
    },
    delivery() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      deliveryGoods({
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
          id: this.detail.orderCode,
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已收货");
            this.loadData();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel",
          });
        });
    },
    // delete() {
    //   const loading = this.$toast.loading({
    //     duration: 0, // 持续展示 toast
    //     forbidClick: true, // 禁用背景点击
    //   });
    //   deleteOrder({
    //     token:
    //       sessionStorage.getItem("token") ||
    //       "177128e71cd2d4bc4acdba48bcae04709",
    //       id: this.detail.orderCode,
    //   })
    //     .then(res => {
    //       loading.clear();
    //       if (res.code == 0) {
    //         this.$toast("已删除");
    //         setTimeout(() => {
    //           this.$router.back();
    //         }, 500);
    //       } else {
    //         this.$toast(res.msg);
    //       }
    //     })
    //     .catch(err => {
    //       this.$vux.loading.hide();
    //       console.log(err);
    //       this.$vux.toast.show({
    //         text: "网络错误",
    //         type: "cancel",
    //       });
    //     });
    // },
    cancel() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      cancelOrder({
        id: this.detail.orderCode,
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已取消");
            this.loadData();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel",
          });
        });
    },
    makePreOrder() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      weixinPay({
        id: this.detail.orderCode,
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            if (!res.data.appId) {
              this.$toast("支付成功", "middle");
              this.loadData();
            } else {
              this.weChatPay(
                res.data.appId,
                res.data.timeStamp,
                res.data.nonceStr,
                res.data.packageValue,
                res.data.signType,
                res.data.sign
              );
            }
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          loading.clear();
          console.log(err);
          this.$toast({
            text: "网络错误",
            type: "cancel",
          });
        });
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign, //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("支付成功", "middle");
              $this.loadData();
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.$toast("取消支付", "middle");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.$toast("支付失败", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    addZero(val) {
      return val < 10 ? "0" + val : val;
    },
    countTime() {
      // console.log('1')
      //获取当前时间
      let date = new Date();
      let now = date.getTime();
      //设置截止时间
      let endDate = new Date(this.endTime.replace(/-/g, "/"));
      let end = endDate.getTime();
      //时间差
      let leftTime = end - now;
      //定义变量 d,h,m,s保存倒计时的时间
      if (leftTime >= 0) {
        // this.day = this.addZero(Math.floor(leftTime/1000/60/60/24));
        this.hour = Math.floor(leftTime / 1000 / 60 / 60);
        this.min = Math.floor((leftTime / 1000 / 60) % 60);
        this.second = Math.floor((leftTime / 1000) % 60);
        setTimeout(this.countTime, 1000);
      }
      //递归每秒调用countTime方法，显示动态时间效果
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getOrderDetail({
        id: this.$route.query.id,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          if(this.detail.addr)
          {
            this.detail.addrText =
              this.detail.addr.areaNames.replace(/\//g, "") +
              this.detail.addr.addrAddr;
          }
          else{
            this.detail.addrText = '';
          }
         
          // if (!this.detail.trace.AcceptStation) {
          //   let year = new Date().getFullYear(),
          //     month = new Date().getMonth() + 1,
          //     day = new Date().getDate(),
          //     hour = new Date().getHours(),
          //     min = new Date().getMinutes(),
          //     sec = new Date().getSeconds();

          //   this.detail.trace.AcceptStation = "正在为您打包，马上为您发出";
          //   this.detail.trace.AcceptTime =
          //     year +
          //     "-" +
          //     (month < 10 ? "0" + month : month) +
          //     "-" +
          //     (day < 10 ? "0" + day : day) +
          //     " " +
          //     (hour < 10 ? "0" + hour : hour) +
          //     ":" +
          //     (min < 10 ? "0" + min : min) +
          //     ":" +
          //     (sec < 10 ? "0" + sec : sec);
          // }
          if (res.data.order_status == 1) {
            this.endTime = res.data.valid_order_time;
            this.countTime();
          }
        }
      });
    },
  },
  mounted() {
    document.title = "订单详情";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
