  .line {
    height: 5px;
    background: #efefef;
  }
  .line1 {
    border-top: 1pt solid #efefef;
    margin: 20px 0 5px;
  }
  .flex {
    display: flex;
    align-items: center;
    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      position: relative;
      height: 100%;
    }
  }
  .logistics {
    .waitPay {
      background: url("../../assets/img/pic/dfk.png") no-repeat;
      background-size: 100% 100%;
