      margin-top: 10px;
    }
  }
  .address {
    padding: 15px;
    .icon {
      width: 20px;
      margin-right: 10px;
    }

    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      font-size: 15px;
    }

    .personalInfo {
      font-size: 16px;
      line-height: 15px;
      font-weight: bold;
      .name {
        margin-right: 10px;
      }
      .tenlo {
        float: right;
      }
    }
    .addressInfo {
      font-size: 14px;
      color: #999;
