      .arrow {
        margin-top: 10px;
      }
    }
  }
  .goodsInfo {
    padding: 0 15px;
    margin-top: 10px;
    .goodsInfo-top {
      align-items: center;
      .title {
        font-size: 18px;
        font-weight: bold;
        line-height: 25px;
      }
      .status {
        font-size: 15px;
        line-height: 25px;
        color: $base-color;
      }
    }
    .list {
      .item {
        height: 120px;
        overflow: hidden;
        margin: 20px 0;
        align-items: flex-start;
        .item-pic {
          margin-right: 10px;
          img {
            width: 100%;
            height: 120px;
          }
        }
        .title {
          font-size: 16px;
          line-height: 25px;
          max-height: 50px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .parameter {
          color: #999;
          font-size: 15px;
          line-height: 20px;
          margin: 10px 0;
        }
        .price {
          font-size: 13px;
          font-weight: 600;
          color: #333;
          flex: 2;
          position: absolute;
          width: 100%;
          bottom: 10px;
          .money {
            font-size: 16px;
          }
          .num {
