          }
        }
      }
    }
    .goodsInfo-bottom {
      padding: 10px 0;
      .flex {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        align-items: center;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .flex1 {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        .flex-2 {
